* {
    font-family: 'Roboto', sans-serif; }

header {
    position: relative;
    width: 100%;
    z-index: 1; }

nav {
    width: 100%;
    position: fixed;
    height: 50px;
    background: #212121;
    z-index: 1; }

.menu {
    display: flex;
    justify-content: space-between;
    padding: 10px 70px;
    margin: 0;
    list-style-type: none;
    .menu_link {
        color: #fff;
        font-size: 15px;
        font-weight: 300; }

    .menu_item {
        border-right: 2px dotted black;
        padding-right: 28px; } }

.subheader {
    position: absolute;
    width: 100%;
    height: 84px;
    background-color: rgba(0, 0, 0, 0.6);
    margin-top: 50px;

    .subheader_logo {
        display: block;
        max-width: 100%;
        margin-top: 16px; }

    .subheader_official {
        opacity: 0.5;
        color: #fff;
        font-size: 13px;
        font-weight: 400;
        margin-top: 6px; }

    .subheader_call {
        color: #fff;
        font-size: 12px;
        font-weight: 300;
        margin-top: 19px;
        text-align: right; }

    .subheader_phone {
        display: block;
        margin-top: 8px;
        color: #fff;
        font-size: 22px;
        font-weight: 400;
        text-align: right;
        line-height: 12px; }

    .subheader_btn {
        margin-top: 19px;
        width: 172px;
        height: 46px;
        box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.18);
        border: none;
        background-image: linear-gradient(to top, #24b3ce 0%, #4adffc);
        border-radius: 4px;
        color: #fff;
        font-size: 14px;
        font-weight: 400;
        text-transform: uppercase; } }

.promo {
    min-height: 800px;
    padding: 200px 0 135px 0;
    background: url(../img/1/bg.jpg) center (center / cover) no-repeat;

    .promo_header {
        margin: 0;
        color: #fff;
        font-size: 60px;
        font-weight: 700;
        text-align: center;
        line-height: 60px; }

    .promo_subheader {
        margin: 0;
        margin-top: 22px;
        color: #fff;
        font-size: 36px;
        font-weight: 700;
        text-align: center;
        line-height: 36px; }

    .promo_descr {
        margin-top: 24px;
        color: #fff;
        font-size: 18px;
        font-weight: 400;
        text-align: center;
        line-height: 24px; }

    .promo_btn {
        display: block;
        margin: 0 auto;
        margin-top: 120px;
        width: 260px;
        height: 63px;
        box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.18);
        border: none;
        background-image: linear-gradient(to top, #24b3ce 0%, #4adffc);
        border-radius: 4px;
        color: #fff;
        font-size: 18px;
        font-weight: 400;
        text-transform: uppercase; } }

.label {
    margin: 0 auto;
    width: 112px;
    height: 24px;
    background-color: black;
    padding: 4px;
    text-align: center;
    color: #fff;
    font-size: 11px;
    text-transform: uppercase; }

.title {
    margin-top: 36px;
    text-align: center;
    font-size: 38px;
    font-weight: 700;
    text-transform: uppercase; }

.subtitle {
	margin: 0 auto {
    color: #222222;
    font-size: 17px;
    font-weight: 400;
    text-align: center; } }


.reason {
    background-color: #fff;
    padding: 73px 0 115px 0;

    .reason_block {
        display: flex;
        justify-content: center;
        align-items: center;

        .reason_round {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 115px;
            height: 115px;
            background: #1eacc7;
            border-radius: 100%;
            margin-right: 24px; }

        .reason_block_descr {
            width: 324px; }

        .reason_descr_subheader {
            color: #1eacc7;
            font-size: 17px;
            font-weight: 400; }

        .reason_descr {
            color: #202020;
            font-size: 15px;
            font-weight: 300; } } }
